import {
  VStack,
  Tabs,
  Tab,
  TabPanels,
  TabPanel,
  TabList,
  TabIndicator,
  Heading,
} from '@chakra-ui/react';
import SideMenu from '../components/side_menu';
import Chat from '../components/chat';
import InputChat from '../components/input_chat';
import useWindowDimensions from '../hooks/use_window_dimensions';
import Histogram from '../components/histogram';

const MainPage = () => {
  const { width, height } = useWindowDimensions();
  return (
    <VStack
      width={'100%'}
      minH={'100vh'}
      backgroundColor={'black'}
      padding={width <= 1024 ? '1vh 5vw' : '5vh 20vw'}
    >
      <SideMenu />
      <VStack
        width={'100%'}
        borderLeft={width <= 1024 ? '0px' : '2px solid #FFAA00'}
        borderTop={width <= 1024 ? '2px solid #FFAA00' : '0px'}
        padding={'20px'}
        minH={'100%'}
      >
        <Tabs variant="enclosed" width={'100%'}>
          <TabList width={'100%'} gap={'40px'}>
            <Tab color={'#FFAA00'} fontSize={20}>
              
            </Tab>
            <Tab color={'#FFAA00'} fontSize={20}>
              
            </Tab>
          </TabList>
          <TabIndicator
            mt="-1.5px"
            height="2px"
            bg="#FFAA00"
            borderRadius="1px"
          />
          <TabPanels>
            <TabPanel>
              <Chat
                messages={[
                  {
                    role: 'Neuro',
                    message:
                      '    messages,        : role ( )  message ( ).          ,    .       ,     -  .',
                  },
                  {
                    role: 'user',
                    message:
                      '                                                                        ',
                  },
                  {
                    role: 'Neuro',
                    message:
                      '      react chakra ui                ',
                  },
                  {
                    role: 'user',
                    message:
                      ' ',
                  },
                ]}
              />
              <InputChat />
            </TabPanel>
            <TabPanel>
              <Histogram />
            </TabPanel>
          </TabPanels>
        </Tabs>
      </VStack>
    </VStack>
  );
};

export default MainPage;
